
import XButton from 'vux/src/components/x-button/index.vue'
import Icon from 'vux/src/components/icon/index.vue'

import api from '../api'
export default {
    name:'jump',
    data:function(){
        return {
            jump_time:5,
        }
    },
    created:function(){
        var self=this;
        // self.jump_time=5;
        // setInterval(function(){
        //     self.jump_time--;
        //     if(self.jump_time<=0){
        //         self.$router.push("/");
        //     }
        // },1000)
        this.get_access_token();
    },
    methods:{
        get_access_token:function(){
            var query=this.$route.query;
            console.log(query);
            api.get_token(query).then(res=>{
                console.log(res);
            })
        }
    },
    components:{
        XButton,
        Icon
    }
}



